  .container {
    width: 1440px;
  }
}

.search-form {
  z-index: 1;
  width: 100%;
  background: linear-gradient(to right, #e0ffe17f, #b1ffb780);
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form__input {
  width: 150px;
  height: 40px;
  border: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

.search-form__input::placeholder {
  color: #bc93d7;
  font-size: 16px;
}

@media screen and (min-width: 768px) {
  .search-form__input {
    width: 400px;
  }
}

@media screen and (min-width: 1440px) {
  .search-form__input {
    width: 700px;
  }
}

.search-form__button {
  width: 100px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background-color: #9cc6e0;
  border: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  font-size: 16px;
  transition: background-color .3s linear, color .3s linear;
}

.search-form__button:hover, .search-form__button:focus {
  color: #fff;
  background-color: #0263aa;
}

@media screen and (min-width: 320px) {
  .search-form__button {
    width: 70px;
  }
}

@media screen and (min-width: 768px) {
  .gallery {
    flex-wrap: wrap;
    margin: -10px;
    display: flex;
  }
}

.photo-card {
  width: 300px;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  margin-bottom: 10px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 3px 8px #00000080;
}

.photo-card :last-child {
  margin-bottom: 0;
}

.photo-card:hover {
  transform: scale(1.1);
}

@media screen and (min-width: 768px) {
  .photo-card {
    width: calc(50% - 20px);
    margin: 10px;
  }
}

@media screen and (min-width: 1440px) {
  .photo-card {
    width: calc(25% - 20px);
  }
}

.info {
  flex-grow: 2;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.load-more {
  width: 200px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-top: 150px;
  margin-bottom: 30px;
  margin-left: 50%;
  font-size: 16px;
  transition: background-color .3s linear, color .3s linear;
  transform: translateX(-50%);
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #0f3443;
}

.load-more.is-hidden {
  display: none;
}

.end-gallery-text {
  width: 60%;
